  <div class="Login" :style="{ height: webHeight+ 'px'}">
    <div class="log-left">
      <div class="title">
        <router-link to="/" tag="div">
          <img class="logo" src="../../assets/icon/logo.png" alt />
        </router-link>
        <p class="title-text">Welcome To Firmiana Toolkit</p>
      </div>
      <div class="content">
        <log-in v-if="$route.query.id"></log-in>
        <sign-in v-else></sign-in>
      </div>
    </div>
    <div class="footer">
      By signing up to Firmiana Toolkit, you agree to our
      <span>terms of service</span>.
    </div>
  </div>
</template>

<script>
import LogIn from './components/LogIn';
import SignIn from './components/SignIn';

export default {
  name: 'Login',
  components: {
    LogIn,
    SignIn,
  },
  data() {
    return {
      webHeight: 0,
    };
  },

  mounted() {
    this.webHeight =
      screen.height -
      (window.outerHeight - window.innerHeight) -
      (screen.height - screen.availHeight) -
      (window.innerHeight - document.body.clientHeight);
  },
};
</script>

