<template>
  <div class="SignIn">
    <div class="right">
      <!-- username -->
      <div class="input-text">
        <div class="input-title">
          <span style="color:red">*</span> Username:
        </div>
        <input class="input-line" type="text" name id v-model="userName" />
      </div>
      <div class="line"></div>
      <p class="verification-none" :class="{verification:validateUserName !== true }">{{validateUserName}}</p>
      <!-- 结束 -->

      <!-- 账号 -->
      <div class="input-text">
        <div class="input-title">
          <span style="color:red">*</span> Email Address:
        </div>
        <input class="input-line" type="text" name id v-model="emailAddress" />
      </div>
      <div class="line"></div>
      <!-- 结束 -->

      <!-- 错误验证 -->
      <p class="verification-none" :class="{verification:validateEmail !== true }">{{validateEmail}}</p>
      <!-- 结束 -->

      <!-- password -->
      <div class="input-text">
        <div class="input-title">
          <span style="color:red">*</span> Password:
        </div>
        <input class="input-line" type="password" v-model="password" />
      </div>
      <div class="line"></div>
      <p class="verification-none" :class="{verification:validatePassword !== true }">{{validatePassword}}</p>
      <!-- 结束 -->

      <!-- c_Password -->
      <div class="input-text">
        <div class="input-title">
          <span style="color:red">*</span> Confirm Password:
        </div>
        <input class="input-line" type="password" v-model="c_Password" />
      </div>
      <div class="line"></div>
      <p class="verification-none" :class="{verification:validateC_Password !== true }">{{validateC_Password}}</p>
      <!-- 结束 -->

      <!-- 登录按钮 -->
      <div class="input-btn" style="margin-top:30px">
        <progressBtn @progress-bar="sginData" :isStatu="isStatu"></progressBtn>
      </div>
    </div>
  </div>
</template>

<script>
// 引入请求函数
import progressBtn from '../../../PublicComponment/progressBtn';
import { message } from 'ant-design-vue';

export default {
  name: 'SignIn',

  components: {
    progressBtn,
  },

  data() {
    return {
      userName: null,
      emailAddress: null,
      password: null,
      c_Password: null,
      isStatu: false,
      isErr: false,
      // 门户的返回状态码
      statusCode: 0,
    };
  },

  computed: {
    validateUserName() {
      if (this.userName === null) {
        return '';
      } else if (this.userName === '') {
        return '*  Input cannot be empty';
      } else if (this.userName.length < 2 || this.userName.length > 5) {
        return '*  The length is between 2 and 5 characters';
      }
      return true;
    },

    validateEmail() {
      let regEmail = /^\w+@\w+(\.[a-zA-Z]{2,3}){1,2}$/;
      if (this.emailAddress === null) {
        return '';
      } else if (this.emailAddress === '') {
        return '*  Input cannot be empty';
      } else if (regEmail.test(this.emailAddress) === false) {
        return '*  The length is between 2 and 5 characters';
      }
      return true;
    },

    validatePassword() {
      let regPassword = /^(?=.*[a-z])(?=.*[A-Z])(?=.*\d)[^]{8,16}$/;
      if (this.password === null) {
        return '';
      } else if (this.password === '') {
        return '*  Input cannot be empty';
      } else if (regPassword.test(this.password) === false) {
        return '*  8-16 characters, at least 1 upper case letter, 1 lower case letter and 1 number';
      } else {
        return true;
      }
    },

    validateC_Password() {
      if (this.c_Password === null) {
        return '';
      } else if (this.c_Password === '') {
        return '*  Input cannot be empty';
      } else if (this.c_Password !== this.password) {
        return '*  8-16 characters, at least 1 upper case letter, 1 lower case letter and 1 number';
      } else {
        return true;
      }
    },
  },

  methods: {
    sginData() {
      let arr = [this.validateUserName, this.validateEmail, this.validatePassword, this.validateC_Password];
      if (
        arr.every((item) => {
          return item === true;
        })
      ) {
        this.$ProjectModels
          .postSignInModels({ userName: this.userName, email: this.emailAddress, password: this.password })
          .then((res) => {
            if (res.status.statusCode === 200) {
              message.success(
                'Congratulations on your successful registration. The page will jump in five seconds! if you can not jump, you can log in by yourself!'
              );
              setTimeout(this.submitUpdate(), 5000);
            }
          });
      } else {
        message.warn('Incomplete information. Please fill in the complete information!');
      }
    },

    submitUpdate() {
      this.$ProjectModels.postSignInModels({ email: this.emailAddress, password: this.password }).then((res) => {
        let obj = {
          token: res.data.access_token,
          refreshToken: res.data.refresh_token,
        };
        this.$router.push({ path: '/projectSet' });
        this.$store.commit(`handleToken/REFRESH_TOKEN`, obj);
      });
    },
  },
};
</script>

