    margin-top: 30px;
    width: 520px;
    .input-text {
      display: flex;
      justify-content: flex-start;
      font-size: 20px;
      margin-top: 60px;
      .input-title {
        color: #4061ad;
        white-space: nowrap;
      }
      .input-line {
        border: none;
        width: 65%;
        margin-left: 20px;
        outline: none;
        background-color: transparent;
      }
    }
    .line {
      border-bottom: 1px solid #c9c9c9;
      height: 2px;
      width: 100%;
      margin-bottom: 15px;
    }
    .verification-none {
      display: none;
    }
    .verification {
      display: inline-block;
