<template>
  <div class="LogIn">
    <!-- 网站的登录 -->
    <div class="left">
      <p class="left-title">Log in with one of the following:</p>

      <!-- 使用firmiana和微信登录 -->
      <div class="log-button">
        <!-- firmiana的按钮 -->
        <button
          v-if="isMode == 1"
          class="firmiana"
          style="margin-right: 1.5625vw"
          @click="showModal"
        >
          <img
            src="../../../assets/icon/firmiana.png"
            alt
          />Log with Firmiana
        </button>
        <!-- 结束 -->

        <!-- gateway的按钮 -->
        <!-- <button
          v-if="isMode == 1"
          class="gateway"
          style="margin-right: 1.5625vw"
          @click="showgateway"
        >
          <img src="../../../assets/icon/gateway.png" alt />Log with gateway
        </button> -->
        <!-- 结束 -->

        <!-- 微信登录的按钮 -->
        <button
          class="wechat"
          @click="handleQR"
        >
          <img
            src="../../../assets/icon/wechat.png"
            alt
          />Log with Wechat
        </button>
        <!-- 结束 -->
      </div>
      <!-- 结束 -->

      <!-- 分割线 -->
      <fieldset>
        <legend>or</legend>
      </fieldset>
      <!-- 结束 -->

      <!-- 二维码显示 -->
      <div v-if="isMode == 2">
        <qriously
          :value="qrcode"
          :size="200"
        />
      </div>
      <!-- 结束 -->

      <!-- 门户网站登录 -->

      <!-- 结束 -->

      <!-- Firmiana的登录 -->
      <div
        class="email-log"
        v-if="isMode == 1"
      >
        <!-- 输入firmiana账号 -->
        <div class="input-text">
          <div class="input-title">Email Address:</div>
          <input
            class="input-line"
            type="text"
            v-model="F_emailAddress"
          />
        </div>
        <div
          class="line"
          style="margin-bottom: 2.1875rem"
        ></div>
        <!-- 结束 -->

        <!-- 输入firmiana密码 -->
        <div class="input-text">
          <div class="input-title">Password:</div>
          <input
            class="input-line"
            :type="passwordshow?'text':'password'"
            v-model="F_password"
            @keyup.enter="handleFirmianaLogin"
          />
          <a-icon
            type="eye-invisible"
            v-show="passwordshow===false"
            @click="showpassword"
          />
          <a-icon
            type="eye"
            v-show="passwordshow===true"
            @click="showpassword"
          />
        </div>
        <div class="line"></div>
        <!-- 结束 -->

        <!-- 登录验证 -->
        <p
          class="verification-none"
          :class="{ verification: statusCode_ === 401 }"
        >
          * Bad username or password
        </p>
        <p
          class="verification-none"
          :class="{ verification: statusCode_ === 400 }"
        >
          * Missing email parameter
        </p>
        <!-- 结束 -->
        <div class="input-check">
          <!-- 是否remember -->
          <div class="remember">
            <input
              class="check-box"
              type="checkbox"
              name="remember"
              @click="handleCheck"
              checked
              autocomplete="off"
            />
            Remember me
          </div>
          <!-- 结束 -->
        </div>
        <!-- 结束 -->

        <!-- 确认按钮 -->
        <div
          class="input-btn"
          style="margin-top: 1.25rem"
        >
          <progressBtn
            @progress-bar="handleFirmianaLogin"
            :isStatu="isStatu"
          ></progressBtn>
        </div>
        <!-- 结束 -->
      </div>
      <!-- 结束 -->
    </div>
    <!-- 结束 -->
  </div>
</template>

<script>
import axios from "axios";
import qs from "qs"; // 引入二维码组件
import progressBtn from "../../../PublicComponment/progressBtn";

const Base64 = require("js-base64").Base64;

export default {
  name: "LogIn",

  data() {
    return {
      qrcode: "123", // 二维码初始定义
      isMode: 1, // 控制登录模块的显示
      isStatu: false, // 进度按钮的开关
      emailAddress: "", // 门户账号的账号
      password: "", // 门户账号的密码
      F_emailAddress: "", // firmiana账号的账号
      F_password: "", // firmiana账号的密码
      statusCode: "200", // 门户的返回状态码
      statusCode_: "200", // firmiana的返回状态码
      remember: "",
      username: "", // 用户名称
      passwordshow: false
    };
  },

  components: {
    progressBtn,
  },

  created() {
    // 在页面加载时从cookie获取登录信息
    if (this.$cookies.get("emailAddress")) {
      // cookie存在信息就渲染,不存在就赋空值
      this.emailAddress = this.$cookies.get("emailAddress");
      this.password = Base64.decode(this.$cookies.get("passWord"));
    } else {
      this.emailAddress = "";
      this.password = "";
    }
  },

  mounted() {
    let test = document.getElementsByName("remember"); // 获取二维码组件的元素
    test[0].checked = false; // 状态置否
  },

  methods: {
    //是否显示密码
    showpassword() {
      this.passwordshow = !this.passwordshow
    },
    // 获取二维码的方法
    handleQR() {
      axios
        .post(
          `https://phenomics.fudan.edu.cn/firmiana/openauth/createToken?app=123&vtype=string&duration=7`
        )
        .then((res) => {
          this.qrcode = JSON.stringify(res.data);
          if (this.isMode == 2) {
            // 登录模块切换
            this.isMode = 0;
          } else {
            this.isMode = 2;
          }
        })
        .catch((err) => {
          this.$message.error(
            "Please try again or check the network." + "(" + err + ")",
            5
          );
        });
    },

    // 切换单选框的状态 // 储存表单信息
    handleCheck() {
      let test = document.getElementsByName("remember");
      // 判断用户是否勾选记住密码，如果勾选，向cookie中储存登录信息，
      // 如果没有勾选，储存的信息为空
      if (test[0].checked) {
        this.$cookies.set("emailAddress", this.emailAddress, 60 * 60 * 24) ||
          this.$cookies.set("F_emailAddress", this.emailAddress, 60 * 60 * 24);
        // base64加密密码
        let passWord = Base64.encode(this.password);
        this.$cookies.set("passWord", passWord, 60 * 60 * 24);
      } else {
        this.$cookies.set("emailAddress", "");
        this.$cookies.set("passWord", "");
      }
    },

    // 登录按钮
    submitUpdate(a) {
      if (a === true) {
        this.$progress.start(); // 进度条加载
        this.$ProjectModels
          .postLogInModels({
            email: this.emailAddress,
            password: this.password,
          })
          .then((res) => {
            let obj = {
              token: res.data.access_token,
              refreshToken: res.data.refresh_token,
            };
            this.$store.commit(`handleToken/REFRESH_TOKEN`, obj);
            sessionStorage.setItem("token", JSON.stringify(obj));
            this.$cookies.set("token", obj, 60 * 60 * 24);
            this.$progress.finish(); // 进度条完成
            this.$router.push({ path: "/projectSet" });
          })
          .catch((err) => {
            if (err.status == 401) {
              this.statusCode = err.status;
              this.$progress.fail();
            } else if (err.status == 400) {
              this.statusCode = err.status;
              this.$progress.fail();
            }
          });
      }
    },

    showModal() {
      // 切换到firmiana登录
      this.isMode = 1;
    },

    showgateway() {
      // 切换到门户网站登录
      this.isMode = 0;
    },

    handleFirmianaLogin() {
      this.$progress.start(); // 进度条加载
      // firmiana网站的登录
      sessionStorage.setItem("firmiana", "1");
      var data = qs.stringify({
        username: this.F_emailAddress,
        password: this.F_password,
      });
      //  提交数据
      axios({
        method: "post",
        url: "https://phenomics.fudan.edu.cn/firmiana/oauth/token?scope=profile&grant_type=password",
        headers: {
          "Content-Type": "application/x-www-form-urlencoded; charset=UTF-8",
        },
        data,
        auth: {
          username: "a1pfWGbBgnGxk6md7JJrgoqi",
          password: "niT2ixX0kS5spO1NzQadAnCWqZCQK6SOHT42TqdzmxFsut6m",
        },
      })
        .then((res) => {
          let iproteome_token = JSON.stringify(res.data);
          this.$cookies.set("iproteome_token", iproteome_token, 60 * 60 * 24);
          this.handledata(res.data);
        })
        .catch((err) => {
          if (err.response.status == 401) {
            this.statusCode_ = err.response.status;
            this.$progress.fail();
          }
          if (err.response.status == 400) {
            this.statusCode_ = err.response.status;
            this.$progress.fail();
          }
          this.$message.error(
            "Please try again or check the network." + "(" + err + ")",
            5
          );
        });
    },

    handledata(e) {
      // 使用firmiana的token进行登录
      let data = {
        iproteome_token: e,
      };
      this.$ProjectModels.postLogInFirmianaModels(data).then((res) => {
        let obj = {
          token: res.data.access_token,
          refreshToken: res.data.refresh_token,
        };

        this.$store.commit(`handleToken/REFRESH_TOKEN`, obj);
        this.$cookies.set("token", obj, 60 * 60 * 24);
        sessionStorage.setItem("token", JSON.stringify(obj));

        this.$ProjectModels
          .getUserInfoModels()
          .then((res) => {
            this.username = res.data.username;
            this.$router.push({ path: "/projectSet" });
            this.$progress.finish(); // 进度条完成
            // 储存网站追踪信息
            let objs = {
              title: this.username,
              sign: 1,
            };
            this.$store.commit("postNavWorkflowData", [objs]);
          })
          .catch((err) => {
            this.$progress.fail(err);
          });
      });
    },
  },

  watch: {
    emailAddress() {
      this.handleCheck();
    },
    password() {
      this.handleCheck();
    },
  },
};
</script>


